.formBlockContainer {
    display: flex;
    justify-content: center;
    margin: 0;
    font-family: 'Roboto', sans-serif;
    align-items: center;    
}

.formBlock {
    display: flex;
    align-items: center;
    width: 650px;
    height: 820px;
    flex-direction: column;
}

.title {
    padding-top: 20px;
    font-size: 38px;
    font-weight: 200;
    margin-top: 10px;
    margin-bottom: 10px;
}

.formApplication {
    align-items: center;
    background-color: #f0fbffc2;
    margin-bottom: 30px;
    width: 500px;
}

.formName, .formSurname, .formEmail, .formPassword, .formPassword1 {
    width: 100%;
    height: 40px;
    margin-bottom: 40px;
}

.InputMain {
    color: black;
    margin-bottom: 5px;
    border: none;
    outline: none;
    font-size: 18px;
    font-weight: 200;
    width: 100%;
    height: 100%;
    line-height: 20px;
    padding-left: 5px;
    border-bottom: 2px solid rgb(95, 73, 185);
    background-color: rgba(123, 110, 120, 0.36);
}

.formNameLabel, .formSurnameLabel, .formEmailLabel, .formPassword1Label, .formPasswordLabel, .formPhotoLabel {
    padding-left: 5px;
    font-size: 16px;
}

.inputRequired {
    color: red;
    padding-left: -1px;
}

.formPhoto {
    margin-bottom: 10px;
    padding-top: 20px;
    width: 508px;
}

.InputPhoto[type=file] {
    margin-left: 13px;
    width: 66%;
    border-bottom: 2px solid rgb(95, 73, 185);
    background-color: rgba(123, 110, 120, 0.36);
}

.button {
	background:linear-gradient(to bottom, #a866ff 5%, #917ac2 100%);
	background-color:#a866ff;
	border-radius:8px;
	cursor:pointer;
	color:#ffffff;
    font-size:24px;
    letter-spacing: 2px;
    padding:13px 32px;
    border: 1px solid white;
}
.button:hover {
	background:linear-gradient(to bottom, #917ac2 5%, #a866ff 100%);
	background-color:#917ac2;
}