.formBlockContainer {
    display: flex;
    justify-content: center;
    margin-top: 50px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    align-items: center;    
}

.formBlock {
    display: flex;
    align-items: center;
    width: 650px;
    height: 820px;
    flex-direction: column;
}

.formApplication {
    align-items: center;
    background-color: #f0fbffc2;
    margin-bottom: 10px;
    width: 500px;
}

.formLogin, .formPassword {
    width: 100%;
    height: 30px;
    margin-bottom: 30px;
}

.InputMain {
    color: black;
    margin-bottom: 5px;
    border: none;
    outline: none;
    font-size: 18px;
    font-weight: 200;
    width: 100%;
    height: 100%;
    line-height: 20px;
    padding-left: 5px;
    border-bottom: 2px solid rgb(95, 73, 185);
    background-color: rgba(123, 110, 120, 0.36);
}

.formLoginLabel, .formPasswordLabel, .formRegLabel {
    padding-left: 5px;
    font-size: 16px;
}

.button {
	background:linear-gradient(to bottom, #a866ff 5%, #917ac2 100%);
	background-color:#a866ff;
	border-radius:8px;
	cursor:pointer;
	color:#ffffff;
    font-size:18px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    letter-spacing: 2px;
    padding:10px 20px;
    border: 1px solid white;
}
.button:hover {
	background:linear-gradient(to bottom, #917ac2 5%, #a866ff 100%);
	background-color:#917ac2;
}