body {
    background-color: #d2f4ffeb;
    margin: 0;
    padding-bottom: 80px;
    font-family: Roboto;
    font-weight: lighter;
}

h1 {
    font-weight: lighter;
}

.headerContainer {
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    width: 100%;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    height: 100%;
}

.userInfo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 200px;
    font-size: 24px;
}

.userInfo a {
    text-decoration: none;
    color: black;
}

.userInfo .btn-login {
    padding: 7px;
    font-weight: 400;
    font-size: 20px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    color: white;
    border: none;
    border-radius: 5px;
    background:linear-gradient(to bottom, #a866ff 5%, #917ac2 100%);
    background-color: #b582f8;
    cursor: pointer;
    text-decoration: none;
}

.userInfo .btn-logout {
    padding: 7px;
    font-weight: 400;
    font-size: 20px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    color: white;
    border: none;
    border-radius: 5px;
    background:linear-gradient(to bottom, red 5%, red 100%);
    background-color: red;
    cursor: pointer;
    text-decoration: none;
}